"use client";

import { useState, useEffect } from "react";

import { useAuth } from "./contexts/authContext";
import LoginForm from "./components/LoginForm";
import RegisterForm from "./components/RegisterForm";
import Dashboard from "./components/Dashboard";
import BarChart from "@/components/chart/Chart";
import Graph from "@/components/dashboard/Graph";
import Time from "@/components/dashboard/Time";
import Principal from "@/components/Principal";


export default function Home() {
  const { user, isLoading } = useAuth();
  const [authMode, setAuthMode] = useState<"none" | "login" | "register">("none");

  if (isLoading) {
    return (
      <div className="flex h-screen items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-500"></div>
      </div>
    );
  }

  return (
    <main className="min-h-screen bg-gray-50">
      {user ? (
        <Dashboard />
      ) : (
        <div className="container mx-auto px-4 py-12">
          {authMode === "none" && (
            <div className="max-w-md mx-auto text-center p-8 bg-white rounded-lg shadow-md">
              <h1 className="text-3xl font-bold mb-6">Bienvenue sur l'application</h1>
              <div className="space-y-4">
                <button
                  onClick={() => setAuthMode("login")}
                  className="w-full bg-indigo-600 hover:bg-indigo-700 text-white font-medium py-2 px-4 rounded transition duration-200"
                >
                  Se connecter
                </button>
                <button
                  onClick={() => setAuthMode("register")}
                  className="w-full bg-gray-200 hover:bg-gray-300 text-gray-800 font-medium py-2 px-4 rounded transition duration-200"
                >
                  S'inscrire
                </button>
              </div>
            </div>
          )}

          {authMode === "login" && (
            <LoginForm onCancel={() => setAuthMode("none")} />
          )}

          {authMode === "register" && (
            <RegisterForm onCancel={() => setAuthMode("none")} />
          )}
        </div>
      )}
    </main>
  );
}



